/* Popup styles */
.popup-component {
    @include fixed(0, 0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    @include visibility-hidden;
    z-index: -1;
    transition: all 0.35s ease-in-out;
    &.is-open {
        @include visibility-visible;
        z-index: layer(popup-content);
        .popup-component_overlay {
            @include black-bg-open;
        }  
        .popup-component_box {
            transform: scale(1);
        }
    }
    &:not(.is-open) .popup-component_box img {
        height: 0;
        width: 0;
    }
    .popup-component_overlay {
        @include black-bg;
    }
    .btn-close {
        cursor: pointer;
        @include ab(10px, n, n, 10px);
        z-index: 9;
        svg {
            width: 18px;
            height: 18px;
            color: var(--body-color);
            path {
                fill: currentColor;
            }
        }
    }
    .popup-component_box {
        max-width: 90%;
        width: 100%;
        background-color: var(--second-bg-color);
        z-index: layer(popup-content);
        padding: 30px;
        border-radius: 8px;
        max-height: calc(100% - 80px);
        overflow-y: auto;
        overflow-x: hidden;
        position: relative;
        transform: scale(0.8);
        transition: all 0.5s ease-in-out;
        @include custom-scrollbar;
        @media (min-width: 750px) {
            max-width: 705px;
        }
    } 
}
/* Age verification */
.popup-age-verification {
    .popup-age-verification_icon {
        width: 120px;
        height: 120px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto 13px;
        font-size: 48px;
        font-weight: 600;
    }
    .popup-age-verification_heading {
        margin-bottom: 2px;
        font-size: calc(var(--body-font-size) + 4px);
        p {
            margin: 0;
        }
    }
    .popup-age-verification_subheading {
        margin-bottom: 20px;
        @media (min-width: 768px) {
            max-width: 372px;
            margin-left: auto;
            margin-right: auto;
        }
        p {
            margin: 0;
        }
    }
    .popup-age-verification_actions {
        flex-wrap: wrap;
    }
    .btn {
        min-width: 130px;
        padding: 7px 19px;
        flex: 0 0 100%;
        border-radius: var(--btn1-radius);
        @media (min-width: 750px) {
            flex: none;
        }
    }
    button {
        margin-bottom: var(--gutter-sm);
        @media (min-width: 750px) {
            margin-bottom: 0;
            @include margin-end(var(--gutter-sm));
        }
    }
}
/* End age verification */
/* Address popup */
.popup-address {
    .popup-component_box_header {
        margin-bottom: 10px;
    }
    .customer_address_table {
        td {
            padding: 0 0 var(--gutter-sm);
            @media (max-width: 750px) {
                display: block;
                padding: 0 0 var(--gutter-xxs);  
            }
            &.label {
                padding-bottom: 5px;
                @media (min-width: 750px) {
                    padding-bottom: var(--gutter-xxs);
                }
            }
            &.value { 
                input[type="text"], select {
                    width: 100%;
                }
            }
        }
        .label label {
            display: block;
            margin: 0;
            font-size: calc(var(--body-font-size) + 2px);
            @include padding-end(var(--gutter-xxs));
        }
        .address-default td { 
            padding: 0;
            position: relative;
            input[type="checkbox"] {
                @include ab(0, 0, n, n);
                width: 100%;
                height: 100%;
                opacity: 0;
                @include point-cursor;
                appearance: auto;
                background: transparent;
                z-index: -1;
                &:checked ~ label {
                    &:before {
                        @include checked-background;
                    } 
                    &:after {
                        @include checked-icon;
                        @include ab(6px, 2px, n, n);  
                    } 
                }
            }
            label { 
                display: flex;
                @include point-cursor;
                margin: 0;
                position: relative;
                &:before {
                    margin-top: 3px;
                    @include form-check-before;
                }
            } 
        }
    }
    .action_bottom {
        text-align: center;
        margin-top: 20px;
        .btn {
            width: 100%;
            margin: 0 auto;
            padding-left: 45px;
            padding-right: 45px;
            @media (min-width: 750px) {
                width: auto;
            }
        }
    }
}
/* End address popup */
/* Popup quick view */
.popup-quick-view {
    .popup-component_box {
        @media (min-width: 750px) {
            max-width: 90%;
        }
        @media (min-width: 1000px) {
            max-width: 980px;
        }
        @media (min-width: 1441px) {
            max-width: 1195px;
        }
    }
    .product-media .product-sold-out .product-media_main_sold-out {
        font-size: var(--body-font-size);
    }
    .product-info {
        .porduct-details {
            height: auto;
        }
        .product-details_inventory button {
            display: none;
        }
        .product-info_media button {
            &.slider-prev-button {
                left: 10px;
            }
            &.slider-next-button {
                right: 10px;
            }
        }
    }
}
/* End popup quick view */
/* Store locator */
.popup-store-location {
    .popup-component_box {
        transform: scale(1);
        @media (min-width: 750px) {
            max-width: 90%;
        }
        @media (min-width: 1000px) {
            max-width: 980px;
        }
        @media (min-width: 1441px) {
            max-width: 1195px;
        }
    }
    .find-store-location {
        margin-top: var(--gutter-sm);
        @media (min-width: 992px) {
            margin-top: 0;
        }
    }
    .find-store-location_heading {
        margin-bottom: var(--gutter-xs);
    }
    .find-store-location_form {
        border-bottom: 1px solid var(--body-border-color);
        margin-bottom: 20px;
        padding-bottom: 2px;
    }
    .form-group {
        margin-bottom: 17px;
    }
    .store-location-list {
		height: 100%;
		max-height: 245px;
		overflow-y: auto;
        @include custom-scrollbar;
        .store-location-item {
            display: flex;
            @include list-style-reset;
            &:not(:last-child) {
                margin-bottom: 21px; 
            }
            .svg-location {
                flex: 0 0 12px;
                color: var(--body-color);
                margin-top: 5px;
                @include margin-end(10px);
            }
            .store-location-item_store-name {
                margin-bottom: 5px;
                text-transform: uppercase;
                color: var(--body-color);
                font-size: calc(var(--body-font-size) + 2px);
            }
            .store-location-item_full-address {
                color: var(--body-color-2);
            }
        }
    }
    .mapboxgl-map {
        overflow: hidden;
    }
}
/* End store locator */
/* ymm search */
.popup-ymm-search {
    .popup-component_box {
        padding: 0;
    }
    header {
        background: var(--main-color);
        padding: 12px var(--gutter-xs);
        font-size: calc(var(--body-font-size) + 2px);
        font-weight: var(--font-bold);
        @media (min-width: 750px) {
            padding-left: var(--gutter-sm);
            padding-right: var(--gutter-sm);
        }
        .btn-close {
            position: static;
            @include margin-start(var(--gutter-xxs));
        }
    }
    main {
        padding: var(--gutter-xs) var(--gutter-xs) var(--gutter-lg);
        @media (min-width: 750px) {
            padding-top: var(--gutter-lg);
        }
    }
    .ymm-search {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .ymm-search_group {
            margin-top: var(--gutter-xs);
            flex: 0 0 100%;
            @media (min-width: 750px) {
                flex: 0 0 calc(25% - 14px);
                margin: 0  7px 0;
            }
            &:not(.ys-active) .select-group {
                opacity: 0.5;
                cursor: not-allowed;
                @include point-event;
            }
            &.ys-error .select-group select {
                border: 1px solid #ff0000;
            }
        }
        .ymm-search_select {
            height: 100%;
        }
    }
    .select-group {
        position: relative;
        height: 100%;
        select {
            height: 100%;
            width: 100%;
            min-height: 40px;
            white-space: initial;
            @include padding-end(45px);
        }
        svg {
            right: 15px;
        }
    }
    .ymm-search_buttons {
        flex:  0 0 100%;
        display: flex;
        flex-wrap: wrap;
        @media (min-width: 750px) {
            flex:  0 0 25%;
            flex-wrap: initial;
        }
        button {
            width: 100%;
            flex: 0 0 100%;
            margin-top: var(--gutter-xs);
            @media (min-width: 750px) {
                margin: 0 7px 0;
                flex: 0 0 calc(50% - 14px);
            }
        }
    } 
}
/* End ymm search */
/* End popup styles */